.main-logo {
    width: 60px;
    height: 60px;
    margin: 0 auto;
    display: block;
    margin-top: 30px;
}

.main-text {
    display: flex;
    align-content: center;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.main-text h1 {
    font-size: 5rem;
}

.main-text p {
    font-size: 1.2rem;
}

.main-background {
    background-image: url('../../../assets/images/Characters-background.png');
    background-repeat: no-repeat;
    background-position: bottom;
    height: 88vh;
}